@import url("https://fonts.googleapis.com/css?family=Open+Sans:300");
html {
  min-height: 100%;
}

html, body {
  height: 100%;
}

body {
  padding: 0;
  margin: 0;
  background: linear-gradient(235deg, #26aae6 10%, #b425e6 70%);
  overflow: hidden;
}

.center {
  width: 100%;
  height: 100%;
}

.oval-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.oval-container .circle-fix {
  position: absolute;
  width: 800px;
  height: 800px;
  background: linear-gradient(235deg, rgba(69, 140, 230, 0.4) 14%, rgba(180, 37, 230, 0.4) 77%);
  margin: -400px 0 0 -400px;
  border-radius: 50%;
}

.oval-container .circle-fix.transparent {
  background: transparent;
}

.oval-container .circle {
  background: linear-gradient(235deg, #26aae6, #b425e6);
  border-radius: 50%;
  width: 350px;
  height: 350px;
  margin: -175px 0 0 -175px;
  position: absolute;
  box-shadow: inset 0 0 70px -5px white, 0 0 1px 0 white;
  text-align: center;
  line-height: 350px;
  font-size: 24px;
  color: white;
  text-transform: uppercase;
  font-family: "Open Sans", Arial, serif;
  font-weight: 300;
  letter-spacing: 30px;
  padding-left: 20px;
}

.oval-container .oval {
  position: absolute;
  width: 650px;
  height: 510px;
  margin-top: -235px;
  margin-left: -325px;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.12));
  border-radius: 50%;
}

:global(.oval) :global {
  animation: 10s rotate360 infinite linear;
}

@keyframes :global(rotate360) {
  to {
    transform: rotate(360deg);
  }
}



